import React, { useState } from 'react';
import { InputText } from 'primereact/inputtext';
import { Calendar } from 'primereact/calendar';
import { InputTextarea } from 'primereact/inputtextarea';
import { Button } from 'primereact/button';
import axios from 'axios';

const RegisterVisit = () => {
    const [formData, setFormData] = useState({
        PetName: '',
        VisitDate: null,
        VetName: '',
        Reason: '',
        Notes: ''
    });

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevState => ({ ...prevState, [name]: value }));
    };

    const handleDateChange = (e) => {
        setFormData(prevState => ({ ...prevState, VisitDate: e.value }));
    };

    const handleSubmit = async () => {
        try {
            //await axios.post(`${process.env.REACT_APP_API_URL}/api/PetVisit/register`, formData);
            await axios.post('http://10.211.55.3/api/PetVisit/register', formData);

            alert('Visit registered successfully!');
        } catch (error) {
            console.error('Error registering visit', error);
            alert('Failed to register visit.');
        }
    };

    return (
        <div>
            <h3>Register a Visit</h3>
            <div>
                <InputText name="PetName" value={formData.PetName} onChange={handleInputChange} placeholder="Pet Name" />
            </div>
            <div>
                <Calendar name="VisitDate" value={formData.VisitDate} onChange={handleDateChange} placeholder="Visit Date" />
            </div>
            <div>
                <InputText name="VetName" value={formData.VetName} onChange={handleInputChange} placeholder="Vet Name" />
            </div>
            <div>
                <InputText name="Reason" value={formData.Reason} onChange={handleInputChange} placeholder="Reason" />
            </div>
            <div>
                <InputTextarea name="Notes" value={formData.Notes} onChange={handleInputChange} rows={5} placeholder="Notes" />
            </div>
            <Button label="Save" onClick={handleSubmit} />
        </div>
    );
};

export default RegisterVisit;