import React, { useState } from 'react';
import axios from 'axios';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { useNavigate } from 'react-router-dom'; // Make sure to import useNavigate


const Login = ({ onLogin }) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const navigate = useNavigate(); // Initialize navigate

    const handleLogin = async () => {
        try {
            //const response = await axios.post(`${process.env.REACT_APP_API_URL}/api/User/validate`, { username, password });
            const response = await axios.post('http://10.211.55.3/api/User/validate', { username, password });

            // Check the response data, which is a plain string in this case
            if (response.status === 200 && response.data === 'User validated successfully.') {
                onLogin(true); // Notify parent of successful login
                navigate('/dashboard'); // Redirect to the dashboard
            } else {
                alert('Invalid credentials');
            }
        } catch (error) {
            console.error('Login error', error);
            alert('An error occurred during login.');
        }
    };

    return (
        <div>
            <h2>Login</h2>
            <InputText value={username} onChange={(e) => setUsername(e.target.value)} placeholder="Username" />
            <InputText value={password} onChange={(e) => setPassword(e.target.value)} type="password" placeholder="Password" />
            <Button label="Login" onClick={handleLogin} />
        </div>
    );
};

export default Login;