import React, { useState } from 'react';
import { Menubar } from 'primereact/menubar';
import RegisterVisit from './RegisterVisit';
import SearchVisits from './SearchVisit';

const Dashboard = () => {
    const [activePage, setActivePage] = useState(null);

    const menuItems = [
        { label: 'Register a Visit', icon: 'pi pi-fw pi-plus', command: () => setActivePage('register') },
        { label: 'Search Visits', icon: 'pi pi-fw pi-search', command: () => setActivePage('search') }
    ];

    return (
        <div>
            <Menubar model={menuItems} />
            {activePage === 'register' && <RegisterVisit />}
            {activePage === 'search' && <SearchVisits />}
        </div>
    );
};

export default Dashboard;