import React, { useState } from 'react';
import axios from 'axios';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';

const SearchVisits = () => {
    const [petName, setPetName] = useState('');
    const [visitDate, setVisitDate] = useState('');
    const [vetName, setVetName] = useState('');
    const [reason, setReason] = useState('');
    const [notes, setNotes] = useState('');
    const [visits, setVisits] = useState([]);

    const handleSearch = async () => {
        try {
            //const response = await axios.get(`${process.env.REACT_APP_API_URL}/api/PetVisit/getAll`, {
            const response = await axios.get('http://10.211.55.3/api/PetVisit/getAll', {
                params: {
                    petName,
                    visitDate: visitDate ? new Date(visitDate).toISOString().split('T')[0] : null,
                    vetName,
                    reason,
                    notes,
                },
            });

            setVisits(response.data); // Populate visits with the response
        } catch (error) {
            console.error('Error fetching visits', error);
        }
    };

    return (
        <div>
            <h2>Search Visits</h2>
            <div style={{ display: 'flex', flexDirection: 'column', width: '300px', marginBottom: '20px' }}>
                <InputText value={petName} onChange={(e) => setPetName(e.target.value)} placeholder="Pet Name" />
                <InputText type="date" value={visitDate} onChange={(e) => setVisitDate(e.target.value)} placeholder="Visit Date" />
                <InputText value={vetName} onChange={(e) => setVetName(e.target.value)} placeholder="Vet Name" />
                <InputText value={reason} onChange={(e) => setReason(e.target.value)} placeholder="Reason" />
                <InputText value={notes} onChange={(e) => setNotes(e.target.value)} placeholder="Notes" />
                <Button label="Search" onClick={handleSearch} />
            </div>

            <DataTable value={visits} paginator rows={10} emptyMessage="No visits found">
                <Column field="petName" header="Pet Name" />
                <Column field="visitDate" header="Visit Date" body={(rowData) => new Date(rowData.visitDate).toLocaleString()} />
                <Column field="vetName" header="Vet Name" />
                <Column field="reason" header="Reason" />
                <Column field="notes" header="Notes" />
            </DataTable>
        </div>
    );
};

export default SearchVisits;